ction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;grid-gap:0.5rem;gap:0.5rem;padding:0.5rem 0.8rem;color:#fefefe;border-radius:10px;background-color:#2522ca;-webkit-backdrop-filter:opacity(50%);backdrop-filter:opacity(50%)}.main-frame .controls .info p{font-size:0.8rem}.main-frame .controls .info #info{font-size:0.8em}.icon{text-decoration:none;font-size:1.4rem;color:#070707;outline:none;transition:all 500ms ease-in-out;background:none;border:none}.icon:hover{cursor:pointer;color:#2522ca}.Toastify__toast--success{background-color:#2522ca}

